@import './variables';
@import './mixins';

.search {

	&__input-container {
		border: 1px solid $color-darkgrey;
		background-color: $color-grey;
	}

	@include mobile {
		display: none;
	}
	
	&__input{
		padding: rem(16px 20px 16px 76px);
		@include input-expand;
		border: none;
		background-color: #FAF9F8;
		color: $color-black;
		background-image: url('../Assets/search.svg');
		background-repeat: no-repeat;
		background-position: rem(28px 13px); 
	}

	&__container {
		position: absolute;
    	background-color: $color-white;
		width: 100%;
		z-index: 4000;
		max-height: 50vh;
		overflow: scroll;
	}

	&__result {
		padding: rem(8px 32px);
		transition: background-color 400ms;
		width: 100%;
	}

	&__result:hover {
		background-color: $color-darkgrey; // VILKEN FRG!!!??????

		cursor: pointer;
	}
}
	