@import './variables';
@import './mixins';


.home {
	
	&__hero-block{
		@include hero-block;
		background-color: $color-darkgrey;
	}

	&__hero-block-image{
		@include hero-block-image;
		background-image: url("../Assets/hero-start.svg");
	}

	&__shopNow-button{
		@include button-primary;
		background-color: $color-pink;
		margin-top: 20px;
		color: $color-red;
	}
}